export default {
    
    getModalActionAndData: state =>  state,

    getModalTitle: state => {
        switch (state.heading) {
            case 'report_title':
                return 'రిపోర్ట్ యొక్క టైటిల్';
            case 'edit_pratilipi_summary':
            	return 'సంగ్రహం (ఆప్షనల్):';
            case 'edit_pratilipi_title':
            	return 'శీర్షిక';
            case 'pratilipi_delete_content':
                return 'తొలిగించండి';
        }
    },

    getInputModalSubheadings: state => {
    	switch (state.heading) {
            case 'report_title':
                return ['రిపోర్ట్ యొక్క టైటిల్'];
            case 'edit_pratilipi_summary':
            	return ['సంగ్రహం (ఆప్షనల్)'];
            case 'edit_pratilipi_title':
            	return ['శీర్షికను రాయండి', 'శీర్షికను ఆంగ్లంలో రాయండి'];
            default:
            	return []
        }	
    },

    getModalMessage: state => {
        switch (state.message) {
            case 'pratilipi_confirm_delete_content':
                return 'మీరు రచనను తొలిగించాలని భావిస్తున్నారా?';
        }
    }
}



